@-webkit-keyframes preloader {
	0% {
		width: 100%;
		-webkit-transform: skew(0deg) translateX(0) scale(1);
		        transform: skew(0deg) translateX(0) scale(1);
	}

	50% {
		-webkit-transform: skew(-20deg) translateX(100%) scale(1.5);
		        transform: skew(-20deg) translateX(100%) scale(1.5);
	}

	100% {
		width: 0%;
		-webkit-transform: skew(0deg) translateX(100%) scale(1.5);
		        transform: skew(0deg) translateX(100%) scale(1.5);
	}
}

@keyframes preloader {
	0% {
		width: 100%;
		-webkit-transform: skew(0deg) translateX(0) scale(1);
		        transform: skew(0deg) translateX(0) scale(1);
	}

	50% {
		-webkit-transform: skew(-20deg) translateX(100%) scale(1.5);
		        transform: skew(-20deg) translateX(100%) scale(1.5);
	}

	100% {
		width: 0%;
		-webkit-transform: skew(0deg) translateX(100%) scale(1.5);
		        transform: skew(0deg) translateX(100%) scale(1.5);
	}
}

.Title {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: #252525;
}

.Title::before {
	content: '';
	right: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: #fff;
	position: absolute;
	-webkit-animation: 1s preloader forwards;
	        animation: 1s preloader forwards;
}

.Pre-intro { 
	padding: none;
	height: auto;
}




